.team-slider {
  &-wrap {
    position: relative;
    margin: 0 0 30px;
  }

  .slick-list {
    margin: 0 0 80px;
  }

  .slick-dots {
    display: flex;
    padding: 0 0 25px;
    margin: 0;
    list-style: none;

    li {
      width: 12px;
      height: 7px;
      background: #E9E9E9;
      border-radius: 4px;
      cursor: pointer;

      & + li {
        margin-left: 8px;
      }

      &.slick-active {
        width: 64px;
        background: linear-gradient(45deg, #3C589E, #93B7E4);
      }
    }

    button {
      font-size: 0;
      padding: 0;
      margin: 0;
      background-color: transparent;
      border: none;
    }
  }
}