html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

html, body {
  height: 100%;
  font-family: $f-din-bold;
}

body {
  padding: 0;
  margin: 0;
  line-height: 1.25;
  background-color: $c-main-bg;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  width: 100%;
  padding: 0 20px;

  @include mq(tablet) {
    max-width: 768px;
    margin: 0 auto;
  }

  @include mq(desktop) {
    max-width: 1300px;
    padding: 0;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.header, .footer {
  flex: 0 0 auto;
}

.main {
  flex: 1 0 auto;
  padding: 46px 0 0;

  @include mq(desktop) {
    padding: 0;
  }

  &_bg {
    background-image: url("../../img/index/bg.jpg");
    background-repeat: no-repeat;
    background-position: center bottom -235px;
    background-size: unset;
  }
}

.center {
  margin: auto;
}

.m-hidden {
  @include mq(tablet, max) {
    display: none;
  }
}

.t-hidden {
  @include mq(desktop, max) {
    display: none;
  }
}

.d-hidden {
  @include mq(tablet) {
    display: none;
  }
}

.white-logo {
  display: none;
}

.no-js, .isOpen {
  .adaptive-list__item_btn-open {
    display: none;
  }

  .adaptive-list__item_btn-close {
    display: block;
  }

  .blue-logo {
    display: none;
  }

  .white-logo {
    display: block;
    margin: auto;

    @include mq(tablet) {
      transform: translateX(50%);
    }
  }

  @include mq(desktop, max) {
    & .header {
      background-color: $c-blue;
    }

    & .nav-list__link, & .nav-dropdown__link {
      color: $c-white;
    }

    & .nav-dropdown__icon {
      stroke: $c-white;
    }

    & .nav-dropdown__icon, & .nav-compare__icon {
      fill: $c-white;
    }

    & .nav__inner {
      display: block;

      @include mq(desktop) {
        display: flex;
      }
    }
  }
}

.line-wrap {
  position: relative;
}

.line {
  position: absolute;
  top: 160px;
  z-index: 2;
  transform: translateX(-60px);
  transition: all .35s ease-in-out;
  text-align: center;

  &::before {
    content: "";
    position: absolute;
    left: -20px;
    right: -20px;
    bottom: 0;
    background-color: #06345B;
    width: calc(100% + 40px);
    height: 2px;
    z-index: 2;
  }

  span {
    position: absolute;
    bottom: -1px;
    z-index: 3;
    left: 50%;
    width: 40px;
    height: 4px;
    border-radius: 2px;
    transform: translateX(-50%);
    background: linear-gradient(45deg, #3C589E, #93B7E4);
  }
}

