.slider {
  &-wrap {
    position: relative;
    margin: 0 0 50px;

    .route:not(:last-child) {
      margin: 0;
    }

    .circle {
      position: absolute;
      top: 50%;
      z-index: 3;
      transform: translateY(-50%);

      &_prev {
        left: 0;
      }

      &_next {
        right: 0;
      }
    }
  }

  &-for {
    margin: 0 -30px;

    .slick-list {
      padding: 46px 0 0;
    }

    .slick-slide .best-info__desc {
      display: none;
    }

    .slick-slide.slick-current + .slick-slide {
      position: relative;
      transform: translateY(-46px);
    }

    .slick-slide.slick-current + .slick-slide .best-info__desc {
      display: block;
    }
  }

  &-nav {
    padding: 0 140px;
  }

  &__row {
    display: flex;
    justify-content: space-between;
    margin: 0 0 30px;
    padding: 0 0 10px;
    border-bottom: 2px solid rgba(187, 187, 187, .5);
  }

  &__field {
    @include fz(16px);
    max-width: 100px;
    color: $c-dark;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    border: none;
    outline: none;

    &.right {
      text-align: right;
    }
  }

  &__icon {
    width: 30px;

    svg {
      width: 100%;
      height: 100%;
      fill: #08396f;
    }
  }

  .ui-slider {
    width: calc(100% - 18px);
    margin: auto;
    height: 6px;
    background: #E9E9E9;
    border-radius: 4px;
    border: none;

    .ui-slider-range {
      background: linear-gradient(45deg, #3c589e, #93b7e4);
    }
  }

  .ui-slider-handle {
    z-index: 1;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    border: 4px solid $c-white;
    box-shadow: 0 0 5px 5px rgba(139, 173, 220, .4);
    background: linear-gradient(45deg, #3c589e, #93b7e4);
    cursor: pointer;
  }
}